@use "base/variables" as v;
@use "base/mixins" as m;

.footer {
  background-color: v.$primario;
  .copyright {
    margin: 0;
    color: v.$blanco;
    padding: 2rem;
    text-align: center;
  }
}
.contenido-footer {
  padding: 5rem;
  @include m.tablet {
    @include m.grid(3, 2rem);
  }
}
.footer-box {
  font-family: v.$fuentecursiva;
  margin-bottom: v.$separacion;
  @include m.tablet {
    margin-bottom: 0;
  }
  h3 {
    font-family: v.$fuentecursiva;
    font-size: 2.4rem;
    margin: 0 0 1rem 0;
    color: v.$secundario;
  }
  p,
  a {
    font-size: 2rem;
    margin: 0;
  }
}
