@use "base/variables" as v;
@use "base/mixins" as m;

.nuestros-testimoniales {
  padding: 2rem 0;
}
.testimonial {
  background-color: v.$cafeclaro;
  padding: 2rem 2rem;
  max-width: 50rem;
  margin: 0 auto;
  border-radius: 1rem;
  .author {
    text-align: right;
    font-size: 2.4;
    color: v.$primario;
    font-family: v.$fuentecursiva;
  }
}
