@use "base/variables" as v;
@use "base/mixins" as m;

.contenido-nosotros {
  padding: 0;
  @include m.tablet {
    @include m.grid(2, 1rem);
  }
  .imagen {
    padding: 0;
    @include m.tablet {
      margin: 0 2rem 0 0;
    }
  }
  figure {
    transform: rotate(-6deg);
    border: 1rem solid v.$blanco;
    box-shadow: 9px 10px 5px -7px rgba(0, 0, 0, 0.35);
  }
  img {
    width: 100%;
    object-fit: cover;
  }
  .texto {
    margin-top: 5rem;
    blockquote {
      padding-left: 0.5rem solid v.$primario;
      font-size: 2rem;
    }
  }
}
