@use "base/variables" as v;
@use "base/mixins" as m;

.header {
  padding: v.$separacion 0;
  position: relative;
  @include m.tablet {
    min-height: 70rem;
  }
  &::before {
    content: "";
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    position: absolute;
    background-color: rgb(0 0 0 / 0.55);
  }
  .contenido-header {
    position: relative;
    h1 {
      text-align: center;
      color: v.$blanco;
      text-transform: uppercase;
      margin-top: 5rem;
      @include m.tablet {
        margin-top: 10rem;
      }
    }
  }
}
