@use "base/variables" as v;
@use "base/mixins" as m;

.nuestro-menu {
  background-image: url(../img/menu_arriba.svg), url(../img/menu_abajo.svg),
    linear-gradient(to top, v.$primario, v.$primario);
  background-repeat: no-repeat;
  background-size: 200%;
  background-position: center 5rem, center 95%, 100%;
  padding: 15rem 5rem 30rem 5rem;
  @include m.tablet {
    padding: 15rem 0rem 20rem 0rem;
    background-size: 100%;
  }
  @include m.desktop {
    background-size: 50%, 50%, 100%;
  }
}

.grid-menu {
  @include m.tablet {
    @include m.grid(2, 2rem);
  }
}
.menu {
  h3 {
    color: v.$blanco;
  }
  li {
    display: flex;
    justify-content: space-between;
    align-items: center;
    p {
      color: v.$blanco;
      font-size: 2.2rem;

      span {
        display: block;
        color: v.$secundario;
        line-height: 1.5;
      }
    }
    .precio {
      font-size: 4rem;
      font-weight: 400;
    }
  }
}
