@use "base/variables" as v;
@use "base/mixins" as m;

.contenido-principal {
  background-color: v.$blanco;
  padding: 5rem 2rem;
  @include m.tablet {
    padding: 10rem;
    margin-top: -10rem;
    position: relative;
  }
}
.iconos {
  padding: v.$separacion;
}
.listado-iconos {
  @include m.tablet {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    colum-gap: v.$separacion;
  }
  p {
    font-size: 2.2rem;
    font-weight: 700;
  }
  li {
    display: flex;
    flex-direction: column;
  }
  img {
    margin: 0 auto;
    width: 15rem;
    object-fit: cover;
  }
}
