@use "base/variables" as v;
@use "base/mixins" as m;

.proceso-grid {
  @include m.tablet {
    @include m.grid(2, 2rem);
  }
}
.proceso-card {
  @include m.tablet {
    .texto,
    .imagen {
      width: 50%;
      display: inline-block;
    }
    .texto {
      p {
        margin: 1.5rem;
        font-size: 1.6rem;
        line-height: 1.1;
        font-weight: 400;
      }
    }
    &:nth-child(1) {
      grid-column: 1/3;
      display: flex;
    }
    &:nth-child(2) {
      grid-column: 1/3;
      display: flex;
      flex-direction: row-reverse;
    }
    &:nth-child(3) {
      grid-column: 1/3;
      display: flex;
    }
    &:nth-child(4) {
      grid-column: 1/3;
      display: flex;
      flex-direction: row-reverse;
    }
    &:nth-child(5) {
      grid-column: 1/3;
      display: flex;
    }
  }

  padding-bottom: 5rem;

  .texto {
    position: relative;
    p {
      margin: 2rem 0;
      padding: 0;
    }
    h3 {
      margin: 0;
      text-align: left;
      color: v.$secundario;
      &::after {
        content: "";
        position: absolute;
        display: block;
        width: 25%;
        height: 2%;
        background-color: v.$primario;
      }
    }
  }
  .imagen {
    img {
      object-fit: cover;
    }
  }
}
